@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@400;500;600&display=swap);
body {
  font-family: "Prompt", sans-serif;
  background-color: #fbfefa;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
}

.author {
  display: flex;
  justify-content: flex-end;
  font-size: 20px;
  padding: 1.5vh;
}

a {
  color: #1a7997;
  margin-left: 0.5rem;
}

.title {
  font-weight: 500;
  font-size: 48px;
  padding: 0vw 2vw 0vw;
}

.search {
  display: flex;
  margin: 2vw;
  background-color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.search-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1.5vh;
  font-size: 24px;
  border: 1px solid #098023;
  border-left-style: hidden;
  border-radius: 0px 30px 30px 0px;
  outline: none;
}

.search-text {
  margin: 0vw 1vw 0vw;
}

.middle {
  display: flex;
  justify-content: space-between;
  padding: 0vw 2vw 0vw;
}

.left {
  width: 50vw;
}

.right {
  height: 65vh;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.buttons {
  outline: none;
  font-size: 24px;
  font-weight: 600;
  border-radius: 30px;
  width: 22rem;
  padding: 1.5vh;
  margin: 2vh;
  color: white;
  border: none;
  cursor: pointer;
}

.list-button {
  background-color: #098023;
}

.list-button:hover {
  background-color: #07631b;
}

.export-button {
  background-color: #091580;
}

.export-button:hover {
  background-color: #070f58;
}

.footer {
  position: fixed;
  padding: 2vw;
  bottom: 0px;
  right: 0px;
}

input {
  padding: 2vh;
  font-family: "Prompt", sans-serif;
  font-size: 24px;
  width: 80%;
  border: 1px solid #098023;
  border-radius: 30px 0px 0px 30px;
  outline: none;
}

.card {
  display: flex;
  border: 1px solid #098023;
  border-radius: 20px;
  padding: 2vh 3vh 2vh;
  margin: 0vh 2vh 2.5vh 0vh;
}

.card-left {
  width: 50%;
}

.card-right {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  width: 50%;
}

.reading {
  font-size: 20px;
  color: #808080;
}

.word {
  font-size: 30px;
  font-weight: 700;
}

.def {
  font-size: 24px;
  padding-top: 1.5vh;
}

.index {
  color: #808080;
}

.add-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}

.add-text {
  font-size: 24px;
  font-weight: 500;
  color: #8a8a8a;
  padding: 0vh 3vh 0vh 1vh;
}

.words {
  height: 90%;
}

.no-words {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section-title {
  font-weight: 500;
  font-size: 40px;
}

.nothing {
  font-size: 32px;
  color: #6e6e6e;
}

.results {
  overflow-x: hidden;
  overflow-y: auto;
  height: 67vh;
}

::-webkit-scrollbar-track ::-moz-scrollbar-track {
  border-radius: 10px;
  background-color: white;
}

::-webkit-scrollbar-thumb ::-moz-scrollbar-thumb {
  border-radius: 10px;
  background-color: #37c256;
}

